
.ruler_h {
 position: relative;
 height: 12px;
 counter-reset: cmindex -1;
}
.ruler_v {
 position: relative;
 width: 12px;
 counter-reset: cmindex -1;
}
.ruler_h .cm:after {
 position: absolute;
 right: 9mm;
 bottom: 10px;
 content: counter(cmindex);
 counter-increment: cmindex;
}
.ruler_v .cm:after {
 position: absolute;
 right: 12px;
 top: -7px;
 content: counter(cmindex);
 counter-increment: cmindex;
}



